.main-hero-container{
    margin-top: 1px;

    background-image: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5 )), url("https://image.shutterstock.com/z/stock-photo-businessman-hands-holding-pen-for-working-in-stacks-of-paper-files-searching-information-business-1116167936.jpg");

  /* Set a specific height */
  height: 100%;

  /* Position and center the image to scale nicely on all screens */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
   
}

.highlighted {
  background-color: rgba(255,255,255,.7);
  border-radius: 0.3em;
  display: inline-block;
  padding: 0.1em;
}